.shows-box{
    background:url(../../static/media/qiu.2c7a7935.jpg) no-repeat;
    background-size:cover;
    background-attachment:fixed
}
.poem-card{
    width: 100%;
    position:relative;
}
.poem-card .item{
    width:30%;
    position: absolute;
    /*float: left;*/
    background:rgba(255,255,255,.6);
    /*margin:10px 1.6%;*/
    padding:10px;
    color:#222;
    overflow: hidden;
    border-radius:8px;
    z-index: 99;
}
.poem-card .item .bomm{
    width: 100%;
    height:28px;
    overflow: hidden;
    line-height:28px;
    padding:5px 0;
}
.poem-card .flag{
    float: right;
}
